import { verifyPhone } from '../../../utils/utils.js'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel() 
export default {
	data() {
		return {
			surplusSecond: 0, // 还剩多少秒可以重发
			readySendCode: true,  // 是否准备好发送验证码
			verifyCodeTimer: null, //定时器对象
			focusArray: ['name', 'age', 'gender', 'phone', 'verify'],
			focusIndex: null,  //焦点位置
			agreement:false, // 是否同意用户协议
			name: '',
			age: '',
			gender: 0,
			phone: '',
			verifyCode: '',
			genderArray:['男', '女'],
		};
	},
	beforeDestroy() {
		clearInterval(this.verifyCodeTimer)
		this.verifyCodeTimer = null
	},
	components:{
		uniPopup
	},
	computed:{
		genderText(){
			return this.genderArray[this.gender]
		}
	},
	methods:{
		agreementChange(){
			this.agreement = !this.agreement
			
		},
		handleBlur(index){
			let focusArray = this.focusArray
			if(index != focusArray.length - 1 ){
				if(!this[focusArray[index + 1]]){
					this.focusIndex = index + 1
					return
				}
				this.focusIndex = null
				return
			}
			this.focusIndex = null	
		},
		sendVerifyCode(){
			if(!this.readySendCode){
				return 
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.focusIndex = 3
				return
			}
			
			const correctPhone = verifyPhone(this.phone)
			if(!correctPhone){
				uni.showToast({
					icon:'none',
					title:'手机号码错误，请确认'
				})
				this.focusIndex = 3
				return
			} 
			userModel.getVerifyCode(this.phone, 'A').then(res=>{
				// 请求成功,并判断code是否正确
				const { code, message, data } = res.data 
				if(code === '0'){
					uni.showToast({
						title: '验证码已发送，请注意查收'
					})
					this.surplusSecond = 120
					this.focusIndex = 4
					this.verifyCodeTimer = setInterval(()=>{
						if(this.surplusSecond > 0){
							this.surplusSecond -= 1
						}else{
							this.surplusSecond = 0
							clearInterval(this.verifyCodeTimer)
						}
					}, 1000)
					this.focusIndex = 4	
				}else{
					uni.showToast({
						icon:'none',
						title: message
					})
				}
				
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title:'获取验证码失败'
				})
			})
		},
		submit(){
			if(!this.name){
				uni.showToast({
					icon:'none',
					title:'请输入姓名'
				})
				this.$nextTick(()=>{
					this.focusIndex = 0
				})
				return
			}
			if(!this.age){
				uni.showToast({
					icon:'none',
					title:'请输入年龄'
				})
				this.$nextTick(()=>{
					this.focusIndex = 1
				})
				return
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 3
				})
				return
			}
			if(!this.verifyCode){
				uni.showToast({
					icon:'none',
					title:'请输入验证码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 4
				})
				return
			}
			if(!this.agreement){
				uni.showToast({
					icon:'none',
					title:'必须同意用户协议'
				})
				return
			}
			uni.showLoading({
				mask: true
			})
			// 再去验证验证码是否正确
			userModel.verifyCode(this.phone, this.verifyCode).then(res=>{
				// 执行真正的登录
				return userModel.register(this.name, this.age, this.phone,this.age)
				
			}).then(res=>{
				// 注册成功
				if(res.code === '0'){
					uni.hideLoading()
					this.$refs.registerSuccess.open()
					setTimeout(()=>{
						uni.reLaunch({
							url: '../../customer/main/main'
						})
					},2000)	
				}else{
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: '注册失败'
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '注册失败'
				})
			})
		},
		toLogin(){
			uni.reLaunch({
				url: '../login/login'
			})
		},
		radioChange(e){
			this.gender = e.detail.value
		}
	}
}
